export default {
    data(){
        return{
            slug:this.$nuxt.$route.params.slug,
            objProfile:{},
            listCateMapping:[],
            portfolio:[]
        }
    },
    mounted(){
        this.$nextTick(() => {
            this.loader()
            this.$get(`public/user/${this.slug}`)
                .then(res =>{
                    this.objProfile = res.data.user
                    this.listCateMapping = this.mapCategory(this.objProfile.category)
                    this.portfolio = this.objProfile.portfolio
                    this.loader(0)
                })
                .catch(err =>{
                    this.loader(0)
                    console.log(err)
            })
        })
    },

    methods:{}
}
